* {
  margin: 0;
  padding: 0;
  font-family: monospace;
  text-decoration: none;
}

button {
  color: #fff;
  filter: drop-shadow(0 0 .75rem #000);
  background-color: #0a0a23;
  border: none;
  border-radius: 5px;
  font-size: 20px;
}

.container {
  display: flex;
}

main {
  width: 100%;
  padding: 20px;
}

.sidebar {
  color: #fff;
  height: 250vh;
  width: 200px;
  background: #000;
  transition: all .5s;
}

.top_section {
  align-items: center;
  padding: 20px 15px;
  display: flex;
}

.logo {
  text-align: center;
  font-size: 60px;
}

.bars {
  margin-left: 50px;
  font-size: 25px;
  display: flex;
}

.link {
  color: #fff;
  gap: 15px;
  padding: 10px 15px;
  transition: all .5s;
  display: flex;
}

.link:hover {
  color: #000;
  background: #87cefa;
  transition: all .5s;
}

.active {
  color: #000;
  background: #87cefa;
}

.icon, .link_text {
  font-size: 20px;
}

.header {
  text-align: center;
}

.cart {
  height: 40px;
  width: auto;
}

.product-container {
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  margin-top: 100px;
  display: flex;
}

.productDisplay {
  width: 23%;
  text-align: center;
  filter: drop-shadow(10px 5px 4px #000);
  background-color: #fff;
  border: 2px solid #000;
  border-radius: 25px;
  margin-bottom: 20px;
  padding: 20px;
}

.productDisplay img {
  max-width: 100%;
  margin-top: 20px;
}

.productDisplay h1 {
  margin: 10px 0;
}

.productDisplay p {
  margin-top: 10px;
  font-size: 18px;
}

.cart-Display {
  width: 30%;
  align-items: center;
  margin-top: 30px;
  margin-left: 220px;
  display: flex;
}

.cart-Display img {
  max-width: 10%;
  margin-top: 10px;
}

.cart-Display h2, .cart-Display p {
  padding-left: 20px;
  font-size: 20px;
}

.checkout {
  color: #fff;
  float: right;
  width: 35%;
  filter: drop-shadow(10px 5px 4px #000);
  background-color: #000;
  border: 2px solid #000;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  display: flex;
  position: fixed;
  top: 25%;
  right: 0;
  transform: translateY(-50%);
}

.trash {
  margin-top: 20px;
  font-size: 45px;
}

.my-account-box {
  height: 100vh;
  justify-content: center;
  align-items: center;
  display: flex;
}

form {
  border: 1px solid #ccc;
  border-radius: 5px;
  flex-direction: column;
  padding: 20px;
  display: flex;
}

input {
  border: 1px solid #ccc;
  border-radius: 5px;
  margin-bottom: 10px;
  padding: 10px;
  font-size: 16px;
}

input[type="submit"] {
  color: #fff;
  cursor: pointer;
  background-color: #000;
  font-weight: bold;
}

input[type="submit"]:hover {
  background-color: #242424;
}

